"use client"

import { Card, CardContent } from '@/components/ui/card'

interface StatsCardProps {
  title: string
  value: string | number
  subtitle?: string
  color?: 'blue' | 'green' | 'yellow' | 'gray'
  trend?: 'up' | 'down' | 'neutral'
  className?: string
}

export function StatsCard({ 
  title, 
  value, 
  subtitle, 
  color = 'blue', 
  trend = 'neutral',
  className = '' 
}: StatsCardProps) {
  const colorClasses = {
    blue: {
      bg: 'bg-blue-50',
      text: 'text-blue-600',
      accent: 'bg-blue-500'
    },
    green: {
      bg: 'bg-green-50',
      text: 'text-green-600',
      accent: 'bg-green-500'
    },
    yellow: {
      bg: 'bg-yellow-50',
      text: 'text-yellow-600',
      accent: 'bg-yellow-500'
    },
    gray: {
      bg: 'bg-gray-50',
      text: 'text-gray-600',
      accent: 'bg-gray-500'
    }
  }

  const trendIcons = {
    up: (
      <div className="w-4 h-4 flex items-center justify-center">
        <div className="w-0 h-0 border-l-2 border-r-2 border-b-4 border-transparent border-b-green-500"></div>
      </div>
    ),
    down: (
      <div className="w-4 h-4 flex items-center justify-center">
        <div className="w-0 h-0 border-l-2 border-r-2 border-t-4 border-transparent border-t-red-500"></div>
      </div>
    ),
    neutral: null
  }

  return (
    <Card className={`${colorClasses[color].bg} border-0 shadow-md hover:shadow-lg transition-shadow duration-300 ${className}`}>
      <CardContent className="p-6">
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <p className="text-sm font-medium text-gray-600 mb-1">
              {title}
            </p>
            <div className="flex items-baseline space-x-2">
              <p className={`text-2xl font-bold ${colorClasses[color].text}`}>
                {value}
              </p>
              {trend !== 'neutral' && (
                <div className="flex items-center space-x-1">
                  {trendIcons[trend]}
                </div>
              )}
            </div>
            {subtitle && (
              <p className="text-xs text-gray-500 mt-1">
                {subtitle}
              </p>
            )}
          </div>
          
          {/* Ícone decorativo */}
          <div className={`w-10 h-10 ${colorClasses[color].accent} rounded-full opacity-20`}></div>
        </div>
      </CardContent>
    </Card>
  )
}
