"use client"

import { useEffect, useState } from 'react'

interface ProgressBarProps {
  current: number
  target: number
  label?: string
  showNumbers?: boolean
  animated?: boolean
  className?: string
}

export function ProgressBar({ 
  current, 
  target, 
  label, 
  showNumbers = true, 
  animated = true,
  className = '' 
}: ProgressBarProps) {
  const [displayValue, setDisplayValue] = useState(0)
  const percentage = Math.min((current / target) * 100, 100)

  useEffect(() => {
    if (animated) {
      const timer = setTimeout(() => {
        setDisplayValue(percentage)
      }, 100)
      return () => clearTimeout(timer)
    } else {
      setDisplayValue(percentage)
    }
  }, [percentage, animated])

  const remaining = Math.max(target - current, 0)

  return (
    <div className={`w-full ${className}`}>
      {label && (
        <div className="flex justify-between items-center mb-2">
          <span className="text-sm font-medium text-gray-700">{label}</span>
          {showNumbers && (
            <span className="text-sm text-gray-500">
              {current}/{target}
            </span>
          )}
        </div>
      )}
      
      <div className="w-full bg-gray-200 rounded-full h-3 overflow-hidden">
        <div 
          className="h-full bg-gradient-to-r from-blue-500 to-green-500 rounded-full transition-all duration-1000 ease-out relative"
          style={{ width: `${displayValue}%` }}
        >
          {/* Efeito de brilho */}
          <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white to-transparent opacity-30 animate-pulse"></div>
        </div>
      </div>

      {remaining > 0 && (
        <p className="text-xs text-gray-600 mt-2">
          Faltam <span className="font-semibold text-blue-600">{remaining}</span> para sua próxima recompensa!
        </p>
      )}

      {current >= target && (
        <p className="text-xs text-green-600 font-semibold mt-2 flex items-center">
          <div className="w-3 h-3 bg-green-500 rounded-full mr-1 animate-pulse"></div>
          Meta atingida! 🎉
        </p>
      )}
    </div>
  )
}
